
.contact-container {
    position: relative;
    width:100vw;
    display: flex;
    flex-direction: column;
    background: #ccc9ab;
}

.msg-modal {
    opacity: 0;
    position:absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    height: 15%;
    top: 15%;
    left: 0;
    right: 0;
    margin: auto;
    font-size: 16px;
    background: #114b5f;
    color: white;
    border-radius: 5px;
    transition: opacity 1000ms ease 1000ms;
}

.msg-modal.visible {
    opacity: 1;
    transition: opacity 1000ms ease;
}

.sec-ttl--connect {
    background: #F3E9D2;
}

.contact-form {
    margin-top: 5%;
    padding: 10px;
}

.form-group {
    margin-bottom: 10px;
}

.form-label {
    display: block;
}

.form-input {
    font-family: Khula;
    border-radius: 3px;
    border: none;
    outline: none;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.3);
}

.form-input--name,
.form-input--email {
    width: 75%;
}

.form-input--msg {
    width:90%;
    min-height: 100px;
}

.form-submit {
    font-family: Khula;
    color: black;
    margin-top: 10px;
    background: #F3E9D2;
    padding: 3px;
    border-radius: 3px;
}

@media only screen and (min-width: 481px) {
    .contact-container:before {
        content: '';
        border-bottom: solid #f3e9d2   40px;
        border-right: solid transparent 65px;
        border-left: solid transparent 65px;
        position: absolute;
        top: -40px;
        left: 15px;
    }
    .contact-container:after {
        content: '';
        border-bottom: solid #f3e9d2   30px;
        border-right: solid transparent 45px;
        border-left: solid transparent 45px;
        position: absolute;
        top: -30px;
        left: 70px;
    }
    
    .contact-form {
        margin-top: 0;
        align-self: center;
        max-width: 50vw;
        min-width: 400px;
    }
    .form-input--msg {
        width: 100%;
    }
}

/* Small devices (tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    .contact-form {
        font-size: 18px;
    }

   .form-input--name,
   .form-input--email {
       width: 60%;
   }
}

/* tablets/desktops and up ----------- */
@media only screen and (min-width: 992px) {
    .form-input--msg {
        min-height: 250px;
    }
}
